<style lang="postcss" scoped>
.home-container{

}
</style>
<template>
<section class="home-container">
    <MasterPage title="代购买客户">
        <div slot="title-icon">
            <Icon type="md-home" />
        </div>
        <div slot="searchContent" class="search-content-slot">
            <Form :model="formItem" :label-width="80">
              <Row>
                <Col span="12">
                  <FormItem label="筛选：">
                    <Select v-model="searchKey">
                        <Option :value='item.key' :key="item.index" v-for="item in columns1">{{item.title}}</Option>
                    </Select>
                </FormItem>
                </Col>
                <Col span="12">
                  <FormItem label="定向搜索：">
                    <Input @on-change="handleClear" v-model="searchValue" placeholder="输入..."/>
                </FormItem>
                </Col>
              </Row>
          </Form>
        </div>
        <div slot="btns">
          <Button type="primary" icon="ios-search" @click="handleSearch">查询</Button>
        </div>
        <div slot="paddingContent">
          <Table :columns="columns1" :data="data1"></Table>
        </div>
        <div slot="pager">
            <Page :total="100" />
        </div>
    </MasterPage>
</section>
</template>
<script>
import MasterPage from '@/components/Master'

export default {
    components:{
        MasterPage
    },
    data(){
      return{
        formItem: {
            input: '',
            select: '',
            radio: 'male',
            checkbox: [],
            switch: true,
            date: '',
            time: '',
            slider: [20, 50],
            textarea: ''
        },
        searchKey: 'name',
        searchValue: '',
        data : [],
        columns1: [
            {
                title: '姓名',
                key: 'name'
            },
            {
                title: '意向楼盘',
                key: 'age'
            },
            {
                title: '意向单元',
                key: 'address'
            },
            {
                title: '意向房号',
                key: 'num'
            },
            {
                title: '手机号',
                key: 'state'
            },
            {
                title: '操作',
                key: 'action',
                width: 150,
                align: 'center',
                render: (h, params) => {
                    return h('div', [
                        h('Button', {
                            props: {
                                type: 'primary',
                                size: 'small'
                            },
                            style: {
                                marginRight: '5px'
                            },
                            on: {
                                click: () => {
                                    this.show(params.index)
                                }
                            }
                        }, '详情'),
                        h('Button', {
                            props: {
                                type: 'error',
                                size: 'small'
                            },
                            on: {
                                click: () => {
                                    this.remove(params.index)
                                }
                            }
                        }, '删除')
                    ]);
                }
            }
        ],
        data1: [
            {
                name: '黄玉超',
                age: "明洲东苑",
                address: '17栋1单元,18栋2单元',
                num: '1301,1202',
                state: '13466882940',
            },
            {
                name: '郑东升',
                age: "明洲北苑",
                address: '11栋1单元',
                num: '2202,2201',
                state: '18568733629'
            },
            {
                name: '郝淑雯',
                age: "明洲南苑",
                address: '12栋1单元',
                num: '5005',
                state: '17618712619'
            },
            {
                name: '李记',
                age: "明洲西苑,",
                address: '21栋1单元,19栋2单元',
                num: '6003,8001',
                state: '13466882912'
            },
             {
                name: '付国安',
                age: "明洲北苑",
                address: '1栋2单元',
                num: '1201',
                state: '18268832614'
            },
             {
                name: '王岩',
                age: "明洲北苑",
                address: '12栋2单元',
                num: '2302,2101',
                state: '18568733619'
            },
            {
                name: '黄月梅',
                age: "明洲西苑",
                address: '11栋1单元,10栋2单元',
                num: '8001,3001',
                state: '15948737613'
            },
             {
                name: '张亚',
                age: "明洲南苑",
                address: '3栋2单元',
                num: '1203',
                state: '13946731683'
            },
             {
                name: '邓步媛',
                age: "明洲北苑",
                address: '19栋2单元',
                num: '1201',
                state: '17618711314'
            },
        ]
      }
    },
    created () {
        this.data = this.data1;
    },
    methods: {
        show (index) {
            this.$Modal.info({
                title: '基本信息',
                content: `姓名：${this.data1[index].name}<br>意向：${this.data1[index].age}  ${this.data1[index].address}
                <br> 手机号： ${this.data1[index].state}`
            })
        },
        remove (index) {
            this.$Notice.success({
                title: '删除成功',
            });
            this.data1.splice(index, 1);
        },
        handleSearch () {
            this.data1 = this.data1.filter(item => 
                item[this.searchKey].indexOf(this.searchValue) > -1
            )
        },
        handleClear (e) {
            if (e.target.value === '') this.data1 = this.data
        },
    }
}
</script>


